import React from 'react'
import styled, { css } from 'styled-components'
import Typist from 'react-typist'

import { media } from '../utils/style'

const StyledTypist = styled(Typist)`
  color: #fff;
  letter-spacing: 17px;
  max-width: 100%;
  font-size: 72px;
  line-height: 80px;
  font-weight: 100;
  text-transform: uppercase;
  font-family: 'Raleway';
  position: absolute;
  top: 28%;
  left: 30%;
  ${media.md`
  left: 25%;
  top: 28%;
`}
  ${media.xs`
  left: 3%;
  top: 15%;
  letter-spacing: 12px;
  font-size: 45px;
  line-height: 50px;
`}
`

class HeroText extends React.Component {
  render() {
    if (this.props.text) {
      return (
        <Typist>
          <StyledTypist {...this.props}>
            <strong>
              {this.props.text.split(' ').slice(0, 1)}
              <br />
            </strong>
            {this.props.text
              .split(' ')
              .slice(1)
              .join(' ')}
          </StyledTypist>
        </Typist>
      )
    }
    return (
      <StyledTypist cursor={{ show: false }} {...this.props}>
        <strong>Hello</strong> <span>I am Lucas Flores</span> 
        <Typist.Delay ms={1000} />
        <Typist.Backspace count={22} delay={1000} />
        <strong>And I like to </strong> 
        <span> photograph things</span> 
        <Typist.Backspace count={19} delay={1000} />
        <span> hike on mountains</span>
        <Typist.Delay ms={1000} />
        <Typist.Backspace count={18} delay={1000} />
        <span>drink beer with friends🍻<br></br></span>
      </StyledTypist>
    )
  }
}

export default HeroText
